<template>
  <div id="map" class='rel'>
    <FeatureMap />
    <PopUp />
  </div>
</template>

<script>
import store from '../../store/index'
import axios from 'axios'
import * as d3 from 'd3'
import FeatureMap from '../Features/FeatureMap.vue'
import PopUp from '../AquariaLayout/PopUpNew.vue'

var w = window.outerWidth
var h = 50
export default {
  name: 'CoverageMap',
  components: {
    FeatureMap,
    PopUp
  },
  data () {
    return {
      seqLength: 0,
      scale: null,
      coverageMapsrw: 0,
      residueColor: null
    }
  },
  computed: {
    feature () {
      return store.state.feature
    },
    structure () {
      return store.state.secondaryStructure
    }
  },
  watch: {
    feature (newCount, oldCount) {
      var _this = this
      if (newCount.set === null) {
        _this.selectStructmap(window.AQUARIA.currentMember)
      } else {
        d3.selectAll('[id^="residue_"')[0].map(function (d, i) {
          d3.select(d).select('rect').attr({
            fill: 'grey'
          })
        })
        window.addEventListener('resize', function () {
          d3.selectAll('[id^="residue_"')[0].map(function (d, i) {
            d3.select(d).select('rect').attr({
              fill: 'grey'
            })
          })
        })
      }
      console.log('This is data', newCount, oldCount)
    },
    structure () { // Update coverage map when new structure selected
      this.selectStructmap(window.AQUARIA.currentMember)
    }
  },
  mounted () {
    var _this = this
    var url = `${window.BACKEND}/getCoverageMap`
    var loadRequest = {
      selector: [_this.$route.params.uniprotAccession]
    }
    axios({
      method: 'get',
      url: url,
      params: loadRequest
    }).then(function (response) {
      console.log(response.data)
      _this.drawMap(response.data)
      window.addEventListener('resize', function () {
        document.querySelector('#map > #coverageMap').remove()
        _this.drawMap(response.data)
        _this.selectStructmap(window.AQUARIA.currentMember)
      })
    })
  },
  methods: {
    drawMap (data) {
      var _this = this
      var svg = _this.renderSVG()
      _this.seqLength = data.seqLength
      _this.scale = d3.scale.linear().domain([1, _this.seqLength]).range([1, window.innerWidth]) // Scale seq length by screen width
      _this.coverageMapsrw = (_this.scale(data.seqLength)) / (data.seqLength) // Single residue width
      data.secondaryStructures.forEach(function (d) {
        // convert strings to numbers
        d.start = +d.start
        d.end = +d.end
        _this.drawResidues(d, svg, data)
      })
    },
    renderSVG () {
      var svg = d3
        .select('#map')
        .append('svg')
        .attr('id', 'coverageMap')
        .attr('width', w)
        .attr('height', h)

      var rect = svg.append('g')

      return rect
    },
    drawResidues (el, svg, data) {
      var thickness = 0
      var _this = this
      if (el.Type === 'SHEET') {
        var height = 18
        // var color = '#f6c719'
        var translate = 17
      } else if (el.Type === 'COIL') {
        height = 14
        // color = '#6f9e52'
        translate = 19
      } else {
        height = 19
        // color = '#6491b6'
        translate = 16
      }
      for (var n = el.start; n <= el.end; n++) {
        svg
          .append('g')
          .attr('id', 'residue_' + n + '_' + el.Type)
          .append('rect')
          .attr('transform', 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + translate + ')')
          .attr('width', _this.coverageMapsrw * (thickness + 1))
          .attr('height', height)
          .attr('fill', 'grey')
          .style('opacity', 1 - (0.7 / data.densityMap[n - 1]))
          .on('mouseover', function () {
            // _this.residueColor = d3.select(this).style('fill') // Store original residue color
            d3.select(this).attr({
              stroke: 'white'
            })
          })
          .on('mouseout', function () {
            d3.select(this).attr({
              stroke: _this.residueColor
            })
          })
      }
    },
    selectStructmap (selectedClus) {
      var _this = this
      // Select correct region in the coverage map
      d3.selectAll('[id^="residue_"')[0].map(function (d, i) {
        d3.select(d).select('rect').attr({
          fill: 'grey'
        })
      })
      var secondaryStruct = store.getters.getSecondaryStructure[0]
      var seqStart = window.AQUARIA.currentMember.seq_start[0]
      var seqEnd = window.AQUARIA.currentMember.seq_end[window.AQUARIA.currentMember.seq_start.length - 1]
      var width = (seqEnd - seqStart) * _this.coverageMapsrw + _this.coverageMapsrw

      if (document.querySelector('#map > #coverageMap > rect')) {
        document.querySelector('#map > #coverageMap > rect').remove()
      }
      d3.select('#map > #coverageMap').append('rect')
        .attr('width', width)
        .attr('height', 25)
        .attr('transform', 'translate(' + (seqStart * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 14 + ')')
        .attr('fill', 'none')
        .attr('stroke', 'grey')
        .attr('stroke-width', 1)
        .attr('rx', 6)
        .attr('ry', 6)

      secondaryStruct.map(function (index) {
        if (index.end) {
          var start = index.start
          var end = index.end
          var type = index.type
        } else {
          start = index.split('\n')[0].split(':')[1].split('-')[0]
          end = index.split('\n')[0].split(':')[1].split('-')[1]
          type = index.split('\n')[0].split(':')[0]
        }
        for (var j = start; j <= end; j++) {
          var elemId = '[id^="residue_' + j + '_"]'
          // var type = document.querySelector(elemId).id.split('_')[2]
          d3.select(elemId + '> rect').attr({
            fill: _this.getStructureColor(type)
          })
        }
      })
    },
    getStructureColor (type) {
      if (type === 'SHEET') {
        return '#f6c719'
      } else if (type === 'COIL') {
        return '#6f9e52'
      } else {
        return '#6491b6'
      }
    }
  }
}
</script>

