<template>
    <div id="popupshell" class='level4'>
        <div id="popupbody" class='level4' :style="{visibility: data.popupTitle ? 'visible' : 'hidden'}">
            <div id="popupheader">
                <span v-html="getPopupTitle"> </span>
                <span class='x' id='x_close' @click="hidePopup"></span>
            </div>
            <div id="content" v-if="data.PopupText">
                <expandable-text-line use-hover v-html="getPopupText">
                </expandable-text-line>
                <div class='hide'>
                    <TreeView />
                    <ExpandableTextLine />
                    <NodeLoader />
                </div>
            </div>
        </div>
        <img v-bind:src="popupTail" id='popupTail' :style="{visibility: data.popupTitle ? 'visible' : 'hidden'}">
    </div>
</template>

<script>
import TreeView from '../MatchingStructures/TreeView'
import ExpandableTextLine from 'vue-expandable-text-line'
import NodeLoader from './NodeLoader'
import store from '../../store/index'
import { mapGetters } from 'vuex'

export default {
  name: 'PopUp',
  components: {
    TreeView,
    ExpandableTextLine,
    NodeLoader
  },
  watch: {
    coordinates: function (newVal, oldVal) {
      var tailPosition = newVal.x - (document.querySelector('#popupTail').getBoundingClientRect().width / 2) + (newVal.width / 2)
      document.querySelector('#popupshell').style.marginLeft = (tailPosition - 10) + 'px'
      document.querySelector('#popupshell').style.top = (newVal.y - newVal.height) + 'px'
      document.querySelector('#popupTail').style.left = tailPosition + 'px'
    }
  },
  data () {
    return {
      popupTail: require('../../assets/img/popupTail.png')
    }
  },
  computed: {
    coordinates () {
      return store.state.popupCoordinates
    },
    data () {
      return {
        variantStructs: store.state.variantStructs,
        variantResidues: store.state.variantResidues,
        popupTitle: store.state.popupTitle,
        PopupText: store.state.popupText
      }
    },
    ...mapGetters([
      'getVariant',
      'getVariantStructs',
      'getVariantResidues',
      'getPopupTitle',
      'getPopupText',
      'getServersDone',
      'getVariantIdx',
      'getMatches'
    ])
  },
  methods: {
    hidePopup () {
      store.commit('setPopupTitle', '')
    },
    getDefaultDesc: function () {
      // console.log('Restaurant ' + this.getVariantIdx + ' ' + this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx])

      if (this.getVariant && this.getVariantResidues && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'defaultDesc') && this.getVariantIdx > -1) {
        return this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx]
      } else {
        return ''
      }
    }
  }
}
</script>

