#popupshell {
    position: fixed;
    /* bottom: 0px; */
}
#popupbody{
    max-width: 30rem;
    position: absolute;
    height: fit-content;
    /* bottom: 2.9rem; */
    bottom: 0px;
    background: #666;
    width: 90vw;
    margin-left: 0px;
    border-radius: 10px;
}
#popupheader {
    margin-left: 1rem;
    margin-top: 0.5rem;
    color: #ccc;
}
#content {
    overflow-wrap: break-word;
    margin: 10px 12px 12px 12px;
    background: #7f7e7f;
    border-radius: 10px;
}
#popupTail {
    position: fixed;
    /* bottom: 1.9rem; */
}
span.x {
    background: url('../../assets/images/UI-close.png') no-repeat;
    display: block;
    float: right;
    height: 19px;
    width: 19px;
    margin-right: 8px;
}

span.x:hover {
    background-position: 0 -21px;
    cursor: pointer;
}
</style>
