<template>
    <div id='featureMap'>
    </div>
</template>

<script>
import store from '../../store/index'
import * as d3 from 'd3'
import { mapGetters } from 'vuex'
var w = window.outerWidth
var h = 50

export default {
  name: 'FeatureMap',
  data () {
    return {
      seqLength: 0,
      scale: null,
      coverageMapsrw: 0,
      residueColor: null
      //   previousTranslate: null
    }
  },
  computed: {
    feature () {
      return store.state.feature
    },
    ...mapGetters([
      'getVariantResidues'
    ])
  },
  watch: { // Watch if new feature is loaded
    feature (newCount, oldCount) {
      var _this = this
      if (newCount.set === null) {
        // history.pushState(null, '', location.href.split('?')[0] + window.location.hash)
        document.querySelector('#map > #featureMap').remove()
      } else {
        // var query = newCount.set.Server + '.' + newCount.set.Category.replace(/ /g, '_')
        // window.history.pushState('', '', '?' + query + window.location.hash)
        window.addEventListener('resize', function () { // if window resized redraw feature map
          document.querySelector('#map > #featureMap').remove()
          _this.drawMap(newCount)
        })
        if (document.querySelector('#map > #featureMap')) { // check if a feature is already selected. If so, remove and redraw
          document.querySelector('#map > #featureMap').remove()
          _this.drawMap(newCount)
        } else {
          _this.drawMap(newCount)
        }
      }
    }
  },
  methods: {
    drawMap (data) {
      var _this = this
      var svg = _this.renderSVG()
      _this.seqLength = window.AQUARIA.structures2match.sequences[0].length
      _this.scale = d3.scale.linear().domain([1, _this.seqLength]).range([1, window.innerWidth])
      _this.coverageMapsrw = (_this.scale(_this.seqLength)) / (_this.seqLength)
      data.set.Tracks[0].forEach(function (d) {
        // convert strings to numbers
        d.start = +d.start
        d.end = +d.end
        _this.drawResidues(d, svg)
      })
    },
    renderSVG () {
      var svg = d3
        .select('#map')
        .append('svg')
        .attr('id', 'featureMap')
        .attr('width', w)
        .attr('height', h)

      var rect = svg.append('g')

      return rect
    },
    drawResidues (el, svg) {
      var thickness = 0
      var _this = this
      var height = 12
      var translate = 17
      for (var n = el.start; n <= el.end; n++) {
        svg
          .append('g')
          .attr('id', 'feature_residue_' + n)
          .append('rect')
          .attr('transform', 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + translate + ')')
          .attr('width', _this.coverageMapsrw * (thickness + 1))
          .attr('height', height)
          .attr('fill', el.color)
          .on('mouseover', function (d) {
            _this.residueColor = d3.select(this).style('fill') // Store original residue color
            d3.select(this).attr({
              stroke: 'white'
            })
            store.commit('setPopupTitle', el.label)
            if (el.desc !== el.label) {
              if (el.desc !== '') {
                store.commit('setPopupText', el.desc)
              } else {
                if (_this.getVariantResidues[el.start]) {
                  store.commit('setPopupText', _this.getVariantResidues[el.start])
                }
              }
            } else {
              store.commit('setPopupText', null)
            }
            store.commit('setPopupCoordinates', this.getBoundingClientRect())
          })
          .on('mouseout', function () {
            d3.select(this).attr({
              stroke: _this.residueColor
            })
          })
      }
    }
  }
}
</script>

<style>
#featureMap {
    position: relative;
    bottom: 47px ;
}
</style>
